import React from 'react'
//import MaterialIcon from 'material-icons-react';





class ClgACNantes extends React.Component {

  constructor(props) {
    super(props);
    this.state = {};

  }

  intitule=(intitule,contenu,size)=>{
    var style = {width:size}
    if(contenu===""||contenu==="-"){return}

    return (
      <div><div className="mui--text-dark-secondary mui--pull-left" style={style}>{intitule} : </div><div>{contenu}</div></div>
    )
  }

  render() {
    //console.log(this.props.info)
    var info=this.props.info



    return (

      <div>
      <div className="mui--text-dark-secondary mui--pull-right" ><a href={info.site} target="blank"><b>e-lyco</b></a></div>
      <a href={info.ac_nantes} target="blank"><h3>Informations ac-nantes</h3></a><br/>

      {this.intitule("Adresse",info.adresse,"60px")}<br/>
       {this.intitule("Principal",info.principal,"120px")}
       {this.intitule("Principal Adj",info.principal_adjoint,"120px")}
       {this.intitule("Gestionnaire",info.gestionnaire,"120px")}<br/>
       {this.intitule("Tel",info.tel,"120px")}
       {this.intitule("Fax",info.fax,"120px")}<br/>


      </div>



)
}
}

export default ClgACNantes
