import React from 'react'
//import MaterialIcon from 'material-icons-react';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import Divider from 'muicss/lib/react/divider';




class ClgInfo extends React.Component {

  constructor(props) {
    super(props);
    this.state = {};

  }

  intitule=(intitule,contenu,size)=>{
    var style = {width:size}
    console.log("C:",contenu)
    if(contenu==""||contenu==="-"||contenu===null){return}

    return (
      <div><div className="mui--text-dark-secondary mui--pull-left" style={style}>{intitule} : </div><div>{contenu}</div></div>
    )
  }

  render() {
    //console.log(this.props.info)
    var info=this.props.info



    return (


            <div>
            <h3>Informations CD72</h3>
<div>{info.categorie}</div><div>{info.etat_pnc} {info.vague_pnc}<small> ({info.date_pnc})</small></div><br/>
            {this.intitule("ville",info.ville,"150px")}
            {this.intitule("canton",info.canton,"150px")}<br/>
            {this.intitule("email College",info.email_college,"150px")}
            {this.intitule("email Gestionnaire",info.email_gestionnaire,"150px")}
            {this.intitule("tel",info.tel_college,"150px")}<br/>
            {this.intitule("Numéro",info.numid,"150px")}
            {this.intitule("Nom court",info.abreviation,"150px")}
            <i>{this.intitule("Ancien Nom",info.ancien_nom,"150px")}</i>


            </div>



)
}
}

export default ClgInfo
