//import * as PropTypes from "prop-types"
import React from "react"
import { graphql } from 'gatsby'
import Layout from "../components/layout"
//import { graphql } from "gatsby"
//import PostDetail from "../components/post-detail"
//import Layout from "../layouts"
import Divider from 'muicss/lib/react/divider';
import Container from 'muicss/lib/react/container';
import Row from 'muicss/lib/react/row';
import Col from 'muicss/lib/react/col';
import ClgInfo from '../components/clginfo'
import ClgTitle from '../components/clgtitle'
import ClgMenu from '../components/clgmenu'
import ClgACNAntes from '../components/clgacnantes'

class ClgInfoTemplate extends React.Component {

  render() {

    const info=this.props.data.clgListeXlsxRefClg
    const acnantes=this.props.data.clgAcNantesXlsxClgs
    return (
      <Layout>

      <ClgMenu id={info.rne} />
      <ClgTitle info={info}  />

      <Divider/>
      <Container fluid={true} style={{marginTop:'22px'}}>
      <Row>
        <Col md="6">
        <ClgInfo info={info} />
        </Col>
        <Col md="6">
        <ClgACNAntes info={acnantes}  />
        </Col>
        </Row>
        </Container>
      </Layout>
    )
  }
}

export default ClgInfoTemplate

// The post template's GraphQL query. Notice the “id”
// variable which is passed in. We set this on the page
// context in gatsby-node.js.
//
// All GraphQL queries in Gatsby are run at build-time and
// loaded as plain JSON files so have minimal client cost.

export const pageQuery = graphql`
  query($rne: String!) {
    clgListeXlsxRefClg(rne: {eq: $rne}) {
    abreviation
    ancien_nom
    canton
    categorie
    date_pnc(formatString: "DD/MM/YY")
    email_college
    email_gestionnaire
    etat_pnc
    info_ac_nantes
    nom
    numid
    rne
    tel_college
    titre
    vague_pnc
    ville
  }
  clgAcNantesXlsxClgs(rne: {eq: $rne}) {
    ac_nantes
    adresse
    email
    fax
    gestionnaire
    nom
    principal
    principal_adjoint
    rne
    site
    tel
    ville
  }
  }
`
